import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router'
import { fetchWorkspaceList } from '../databench/api/workspace'
import { IWorkspace } from '../databench/types/server'

const WorkspacePage = () => {
  const [worksapceList, setWorkspaceList] = useState<IWorkspace[]>([])
  const history = useHistory()
  useEffect(() => {
    fetchWorkspaceList().then((data) => {
      setWorkspaceList(data)
    })
  }, [])
  return (
    <>
      <div className="p-10 h-screen w-screen bg-gray-200">
        <div className=" header text-center text-5xl mt-10 text-blue-500 font-bold">
          Visual Databench
          <div className="text-3xl mt-10 text-gray-500">大数据工作台</div>
        </div>
        <div className="text-gray-500 mt-10 px-20">切换不同工作空间清空缓存</div>
        <div
          className="flex p-20  items-start "
          style={{ marginTop: '0px', height: 'calc(100% - 150px)' }}
        >
          {worksapceList.map((i) => {
            return (
              <div
                key={i.id}
                onClick={() => {
                  history.push(`/databench?workspace=${i.name}`)
                }}
                style={{ padding: '30px 50px' }}
                className="mr-20  text-gray-400 
                  bg-white shadow-md
                 cursor-pointer rounded-sm hover:shadow-lg"
              >
                <div className="text-gray-500 text-lg">{i.name}</div>
                <div className="text-2xl font-bold text-black-1000  py-10">{i.description}</div>
              </div>
            )
          })}
        </div>
      </div>
    </>
  )
}

export default WorkspacePage
